.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.venue-wrapper {
		position: relative;
		.venue-content {
			position: relative;
			text-align: center;
			.card {
				border: none;
				background-color: var(--color-primary);
				height: 100%;
				border-radius: 10px;
				.card-body {
					padding: 0 2rem 3rem;
					@include max-md {
						padding: 0 1rem 2rem;
					}
					.ribbon-venue {
						position: relative;
						width: fit-content;
						margin: 0 auto 1.5rem;
						padding: 2rem 1rem;
						background-color: var(--color-secondary);
						mask-image: url(../images/ribbona-mask.png);
						mask-position: center;
						mask-size: 100% 100%;
						mask-repeat: no-repeat;
						svg {
							width: 40px;
							height: auto;
							path {
								stroke: var(--text-button);
							}
						}
					}
				}
			}
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
		}
	}
	.row-venue {
		gap: 3rem;
		&-sm {
			@include max-md {
				gap: 3rem;
			}
		}
	}
	.event-name {
		position: relative;
		.date {
			display: flex;
			align-items: center;
			gap: 0.5rem;
			text-align: left;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			p {
				margin-top: 0.2rem;
				font-size: 12px;
			}
		}
		p {
			margin-bottom: 0;
		}
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-button-trans);
		color: var(--color-button-trans);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary) !important;
			border-color: var(--color-secondary);
			color: var(--text-button);
		}
	}
	.maps-embed {
		width: 100%;
		height: auto;
		position: relative;
		aspect-ratio: 16/9;
	}
}
