.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100px;
		@include max-lg {
			width: 80px;
		}
		@include max-md {
			width: 65px;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		width: 100px;
		@include max-lg {
			width: 80px;
		}
		@include max-md {
			width: 65px;
		}
	}
	.orn-3 {
		position: absolute;
		left: 33%;
		top: 8rem;
		width: 280px;
		transform: translateX(-50%);
		@include max-lg {
			left: 0;
			top: 40%;
			width: 320px;
			transform: translateX(-40%);
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 32%;
		width: 280px;
		transform: translateX(50%);
		@include max-lg {
			right: 0;
			top: 0;
			width: 320px;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		.header {
			text-align: center;
			width: 100%;
			height: 100%;
			position: relative;
			.image-wrapper {
				position: absolute;
				top: 0;
				left: 50%;
				aspect-ratio: 4/6;
				width: auto;
				height: 90%;
				transform: translateX(-50%);
				mask-image: url("../images/ribbona-mask.png");
				mask-size: 100% 100%;
				mask-position: center;
				mask-repeat: no-repeat;
				&::after {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background: rgba($color: #000000, $alpha: 0.35);
					content: "";
				}
				@include max-md {
					width: 100%;
				}
				.background-header {
					width: 100%;
					height: 100%;
					object-fit: cover;
					position: relative;
				}
				.border-ribbon {
					position: absolute;
					top: 0;
					left: 50%;
					transform: translateX(-50%);
					width: calc(100% - 23px);
					height: calc(100% - 20px);
					z-index: 2;
				}
			}
			.header-title {
				position: absolute;
				bottom: 8rem;
				color: var(--color-light);
				z-index: 5;
				width: 100%;
				left: 50%;
				transform: translateX(-50%);
				@include max-lg {
					bottom: 10rem;
				}
				@include max-md {
					bottom: 5rem;
				}
				.title {
					color: #fff;
				}
			}
		}
	}
}

.envelope-wrap {
	position: fixed;
	z-index: 1300;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	color: #fff;
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: rgba($color: #000000, $alpha: 0.4);
			content: "";
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.header-content {
		.header {
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			align-items: center;
			padding: 4rem 0 3rem;
			.header-title {
				margin-bottom: 0;
				padding: 5rem 0;
				bottom: 0;
				@include max-md {
					padding: 3rem 0;
				}
			}
		}
	}
}
