.thank-section {
	position: relative;
	padding: 0 0 5rem;
	text-align: center;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 55%;
		width: 240px;
		z-index: 3;
		@include max-md {
			width: 180px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 2rem;
		left: 50%;
		width: 240px;
		transform: translateX(-30%);
		z-index: 3;
		@include max-md {
			width: 180px;
		}
	}
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&::before {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background-color: rgba($color: #000000, $alpha: 0.4);
			content: "";
			z-index: 2;
		}
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.thank {
		position: relative;
		width: 420px;
		padding: 3rem 1.5rem 8rem;
		margin: -1px auto 0;
		background-color: var(--color-primary);
		color: var(--color-dark);
		mask-image: url(../images/ribbona-mask.png);
		mask-size: 100% 100%;
		mask-position: center;
		mask-repeat: no-repeat;
		z-index: 5;
		@include max-md {
			padding: 3rem 1.5rem 5rem;
			width: 100%;
		}
		.border-ribbon {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: calc(100% - 20px);
			height: calc(100% - 20px);
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	small {
		position: relative;
	}
}
