.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.row {
			gap: 3rem;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 340px;
		transform: translateY(50%);
		@include max-lg {
			width: 220px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 340px;
		transform: translate(30%, -50%);
		@include max-lg {
			width: 220px;
		}
	}
	.story-image-abs {
		margin-bottom: -3rem;
		@include min-md {
			position: absolute;
			left: 0;
			margin-bottom: 0;
		}
	}
	.story-description {
		position: relative;
		text-align: center;
	}
	.image-story-wrap {
		position: relative;
		width: 100%;
		aspect-ratio: 1;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: rgba($color: #000000, $alpha: 0.3);
			content: "";
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
