.wishes-section {
	position: relative;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.image-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.wishes-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 2.5rem;
		transform: translateX(-88%) scaleX(-1);
		width: 100px;
		@include max-md {
			width: 80px;
		}
	}

	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 220px;
		transform: translate(40%, -40%);
	}

	.card-form {
		margin-bottom: 0;
		background-color: var(--color-primary);
		position: relative;
		z-index: 3;
		border-radius: 10px;
		&.card {
			border: none;
		}
		.card-body {
			@include min-lg {
				padding: 2rem;
			}
		}
	}

	.splide {
		padding: 0 !important;
		.splide__track {
			overflow: visible;
		}
	}

	.splide__pagination {
		bottom: -1.2rem;
		.splide__pagination__page {
			border: 2px solid var(--color-secondary);
			background-color: transparent;
			width: 10px;
			height: 10px;
			&.is-active {
				background-color: var(--color-secondary);
			}
		}
	}

	.wishes-preview {
		position: relative;
		padding: 5rem 0 3rem;
		.card {
			border: none;
			position: relative;
			z-index: 2;
			margin-bottom: 0.75rem;
			.card-body {
				color: var(--color-dark);
				.wishes {
					text-align: left;
					height: 240px;
					&::-webkit-scrollbar {
						display: none;
					}
					.wish {
						margin-bottom: 1.5rem;
						display: flex;
						flex-direction: column;
						justify-content: space-between;
						height: 100%;
						h6 {
							font-weight: 700;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						div {
							height: 100%;
							overflow: hidden;
							p {
								overflow: auto;
								height: 100%;
								&::-webkit-scrollbar {
									display: none;
								}
							}
						}
					}
				}
			}
		}
	}
}
