.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 20%;
		width: 100px;
		left: 0;
		@include max-lg {
			width: 80px;
		}
		@include max-md {
			width: 70px;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 60%;
		width: 160px;
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 100px;
		}
	}
	.appology-wrap {
		position: relative;
	}
}
