.couple-section {
	position: relative;
	padding: 0 0 5rem;
	text-align: center;
	@include max-md {
		padding: 0 0 3rem;
	}
	.and {
		@include min-md {
			position: absolute;
			top: 40%;
			left: 50%;
			transform: translate(-50%, -40%);
		}
		margin: 1rem 0;
		font-weight: 600;
		color: var(--color-dark);
	}
	.couple {
		position: relative;
		.image-wrap {
			width: 80%;
			margin: auto;
			position: relative;
			margin-bottom: 1.25rem;
			aspect-ratio: 3/4;
			mask-image: url(../images/ribbona-mask.png);
			mask-size: 100% 100%;
			mask-position: center;
			mask-repeat: no-repeat;
			@include max-md {
				width: 80%;
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
			}
			.border-ribbon {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				width: calc(100% - 20px);
				height: calc(100% - 20px);
			}
		}
		.couple-parent {
			width: 80%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				small {
					align-items: center;
					gap: 0.25rem;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
